@import "~styles/variables";

.launches-section {
  &__item {
    display: flex;
    flex-direction: column;
    height: 120px;
    margin-bottom: 70px;
    text-transform: uppercase;

    &-header {
      text-decoration: none;
      color: #fff;
      margin: 0;
      font-size: 14px;
      letter-spacing: 9px;
      margin-bottom: 10px; }

    &-date:hover {
       cursor: pointer; }

    &-value {
      color: #fff;
      letter-spacing: 2px; }

    &-label {
      margin-right: 5px;
      color: $cool_grey;
      letter-spacing: 2px; }

    &-line {
      display: inline-block;
      padding: 0 10px;
      color: $cool_grey; }

    &-date {
      margin: 0;
      font-size: 14px;
      letter-spacing: 9px;
      margin-bottom: 10px;
      color: #fff; }

    &-details {
      margin-top: 15px;
      margin-bottom: 30px; } }

  &__content {
    display: flex;
    flex-direction: row;
    background-color: #000;
    padding-top: 100px;
    color: #fff;
    flex-wrap: wrap;

    &-message {
      text-align: center;
      color: $desert;
      letter-spacing: 2px;
      text-transform: uppercase;
      font-weight: 400;
      font-size: 18px; }

    &-column {
      width: 50%;
      flex-wrap: wrap;

      &-left {
        padding-left: 45px; }

      &-right {
        text-align: right;
        border-left: 2px dashed $cool_grey;
        padding-top: 100px;
        padding-right: 20px; } } }

  &__body {
    background-color: #000;

    &-content {
      padding-top: 70px; } } }

.arrow-icon {
  display: flex;
  position: relative;
  height: 10px;
  width: 100%;

  &-right {
    width: 93%;
    margin-left: 40px;

    &:after {
      display: inline-block;
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      left: 5px;
      height: 2px;
      width: 100%;
      background: $desert;
      transform: translateY(-1px); }

    &:before {
      display: inline-block;
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      left: -45px;
      height: 8px;
      width: 8px;
      border-radius: 50%;
      background: $cool_grey; } }

  &-left {
    justify-content: flex-end;
    width: 95%;
    margin-right: 40px;

    &:before {
      display: inline-block;
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      right: 5px;
      height: 2px;
      width: 100%;
      background: $desert;
      transform: translateY(-1px); }

    &:after {
      display: inline-block;
      content: '';
      position: absolute;
      top: 0;
      right: -45px;
      height: 8px;
      width: 8px;
      border-radius: 50%;
      background: $cool_grey; }
    img {
      transform: rotate(180deg); } } }


.item-details-left {
  padding-right: 100px; }

.item-details-right {
    padding-left: 200px; }

.additional-message {
  padding-top: 50px;
  min-height: 100px; }

.additional-message-loader {
  padding-top: 20px; }
